import React from 'react';

const amountBox = ({text,type,amount}) =>{
    return(
        <div className="col">
            <div className="card">
                <div className={`card-header bg-${type} text-white`}>
                    {text}
                </div>
                <div className="card-body">
                    {amount}
                </div>
            </div>
        </div>
    );
};
export default amountBox



